import React, { useEffect } from "react";
import "./SoftwareSkill.scss";


var skillsArr = ["HTML5", "CSS", "JavaScript", "JSON", "MySQL", "C#", "Arduino", "Unity", "Maya", "Photoshop", "DaVinci Resolve", "Max 8", "VCV Rack", "Reaper"];

export default function SoftwareSkill() {
  useEffect(() => {
    const ul = document.querySelector('.dev-icons'); // Slectionne l'lment <ul> par sa classe

    skillsArr.forEach((skill) => {
      var li = document.createElement('li'); // Cre un lment <li>
      li.textContent = skill; // Dfinit le contenu du <li> avec une comptence
      ul.appendChild(li); // Ajoute le <li>  la liste <ul>
    });
  }, []);

  return (
    <div>
      <div className="software-skills-main-div">
        <ul className="dev-icons"></ul>
      </div>
    </div>
  );
}
